import axios from 'axios';

// Develop server URL
// const postBaseUrl = 'http://localhost:3000/api';

// Staging server URL
// const postBaseUrl = 'http://weathermood-staging.us-west-2.elasticbeanstalk.com/api';

// Production server URL
var postBaseUrl = 'http://weathermood-13.us-west-2.elasticbeanstalk.com/api';

export function listPosts() {
    var searchText = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : '';
    var start = arguments[1];

    var url = postBaseUrl + '/posts';
    var query = [];
    if (searchText) query.push('searchText=' + searchText);
    if (start) query.push('start=' + start);
    if (query.length) url += '?' + query.join('&');

    console.log('Making GET request to: ' + url);

    return axios.get(url).then(function (res) {
        if (res.status !== 200) throw new Error('Unexpected response code: ' + res.status);

        return res.data;
    });
}

export function createPost(mood, text) {
    var url = postBaseUrl + '/posts';

    console.log('Making POST request to: ' + url);

    return axios.post(url, {
        mood: mood,
        text: text
    }).then(function (res) {
        if (res.status !== 200) throw new Error('Unexpected response code: ' + res.status);

        return res.data;
    });
}

export function createVote(id, mood) {
    var url = postBaseUrl + '/posts/' + id + '/' + mood.toLowerCase() + 'Votes';

    console.log('Making POST request to: ' + url);

    return axios.post(url).then(function (res) {
        if (res.status !== 200) throw new Error('Unexpected response code: ' + res.status);

        return res.data;
    });
}


